import React, { createContext, useContext, useState, useEffect } from 'react';
import NotificationSystem, { Notification } from './NotificationSystem';
import NotificationToast, { Toast, useToast } from './NotificationToast';

interface NotificationContextType {
  addNotification: (notification: Omit<Notification, 'id' | 'timestamp'>) => void;
  addToast: (toast: Omit<Toast, 'id'>) => void;
  showQuickAlert: (type: Toast['type'], title: string, message: string) => void;
}

const NotificationContext = createContext<NotificationContextType | null>(null);

interface NotificationProviderProps {
  children: React.ReactNode;
  userType: 'merchant' | 'supplier' | 'shipping' | 'customer';
  userName?: string;
}

export const NotificationProvider: React.FC<NotificationProviderProps> = ({
  children,
  userType,
  userName
}) => {
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const { toasts, addToast, removeToast } = useToast();

  //    
  const addNotification = (notification: Omit<Notification, 'id' | 'timestamp'>) => {
    const newNotification: Notification = {
      ...notification,
      id: Date.now().toString(),
      timestamp: new Date()
    };
    setNotifications(prev => [newNotification, ...prev]);
  };

  //   
  const showQuickAlert = (type: Toast['type'], title: string, message: string) => {
    addToast({
      type,
      title,
      message,
      duration: 4000
    });
  };

  //    
  useEffect(() => {
    const smartNotifications: { [key: string]: Array<{ delay: number; notification: Omit<Notification, 'id' | 'timestamp'> }> } = {
      merchant: [
        {
          delay: 10000, // 10 
          notification: {
            type: 'order',
            title: ' ! ',
            message: '  320    ',
            read: false,
            priority: 'high',
            userType: 'merchant',
            actionButton: {
              label: ' ',
              action: () => showQuickAlert('success', '  !', '   ...')
            }
          }
        },
        {
          delay: 25000, // 25 
          notification: {
            type: 'warning',
            title: '  ',
            message: ' "  "    (5  )',
            read: false,
            priority: 'medium',
            userType: 'merchant'
          }
        }
      ],
      supplier: [
        {
          delay: 8000, // 8 
          notification: {
            type: 'order',
            title: '   ! ',
            message: ' 200   "  "',
            read: false,
            priority: 'high',
            userType: 'supplier',
            actionButton: {
              label: ' ',
              action: () => showQuickAlert('success', '  !', '    ')
            }
          }
        }
      ],
      shipping: [
        {
          delay: 12000, // 12 
          notification: {
            type: 'info',
            title: '  ! ',
            message: ' VIP    -   80 ',
            read: false,
            priority: 'urgent' as any,
            userType: 'shipping',
            actionButton: {
              label: ' ',
              action: () => showQuickAlert('success', '  !', '   ...')
            }
          }
        }
      ],
      customer: [
        {
          delay: 15000, // 15 
          notification: {
            type: 'shipping',
            title: '  ! ',
            message: '   5   ',
            read: false,
            priority: 'high',
            userType: 'customer',
            actionButton: {
              label: ' ',
              action: () => showQuickAlert('info', '  !', '    ')
            }
          }
        }
      ]
    };

    const userNotifications = smartNotifications[userType] || [];
    
    const timeouts = userNotifications.map(({ delay, notification }) => 
      setTimeout(() => {
        addNotification(notification);
        //     
        if (notification.priority === 'high' || notification.priority === 'urgent') {
          addToast({
            type: notification.type as Toast['type'],
            title: notification.title,
            message: notification.message,
            duration: 6000,
            action: notification.actionButton ? {
              label: notification.actionButton.label,
              onClick: notification.actionButton.action
            } : undefined
          });
        }
      }, delay)
    );

    return () => {
      timeouts.forEach(timeout => clearTimeout(timeout));
    };
  }, [userType]);

  //  
  useEffect(() => {
    //          
    const storageKey = 'hasWelcomed-' + userType;
    const hasWelcomed = sessionStorage.getItem(storageKey);

    const welcomeMessages: { [key: string]: Toast } = {
      merchant: {
        id: 'welcome',
        type: 'success',
        title: '    ! ',
        message: '       ',
        duration: 5000
      },
      supplier: {
        id: 'welcome',
        type: 'info',
        title: '    ! ',
        message: '     ',
        duration: 5000
      },
      shipping: {
        id: 'welcome',
        type: 'order',
        title: '    ! ',
        message: '     ',
        duration: 5000
      },
      customer: {
        id: 'welcome',
        type: 'success',
        title: ' ! ',
        message: '       ',
        duration: 5000
      }
    };

    const welcomeToast = welcomeMessages[userType];
    // don't show if session already flagged or a welcome toast is already present
    const alreadyPresent = toasts.some(t => t.id === (welcomeToast?.id) || t.title === welcomeToast?.title);
    if (!hasWelcomed && welcomeToast && !alreadyPresent) {
      const timer = setTimeout(() => {
        addToast(welcomeToast);
        sessionStorage.setItem(storageKey, 'true');
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [userType]);

  return (
    <NotificationContext.Provider
      value={{
        addNotification,
        addToast,
        showQuickAlert
      }}
    >
      {children}
      
      {/*   */}
      <NotificationSystem userType={userType} userName={userName} />
      
      {/*  */}
      <NotificationToast toasts={toasts} onRemove={removeToast} />
    </NotificationContext.Provider>
  );
};

// Hook  
export const useNotifications = () => {
  const context = useContext(NotificationContext);
  if (!context) {
    throw new Error('useNotifications must be used within NotificationProvider');
  }
  return context;
};

export default NotificationProvider;