import React, { useState, useEffect, useRef } from 'react';
import { Bell, X, Check, AlertTriangle, Info, CheckCircle, Box, Truck, Store, DollarSign, Clock, MapPin, TrendingUp, Users, Star, MessageSquare, Settings } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

export interface Notification {
  id: string;
  type: 'success' | 'warning' | 'error' | 'info' | 'order' | 'payment' | 'shipping' | 'message';
  title: string;
  message: string;
  timestamp: Date;
  read: boolean;
  priority: 'low' | 'medium' | 'high' | 'urgent';
  userType: 'merchant' | 'supplier' | 'shipping' | 'customer' | 'all';
  data?: any;
  actionButton?: {
    label: string;
    action: () => void;
  };
}

interface NotificationSystemProps {
  userType: 'merchant' | 'supplier' | 'shipping' | 'customer';
  userName?: string;
}

const NotificationSystem: React.FC<NotificationSystemProps> = ({ userType, userName = '' }) => {
  const [notifications, setNotifications] = useState<Notification[]>([]);
  const [isOpen, setIsOpen] = useState(false);
  const [unreadCount, setUnreadCount] = useState(0);
  const [filter, setFilter] = useState<'all' | 'unread' | 'high'>('all');
  const audioRef = useRef<HTMLAudioElement>(null);

  //      
  const generateSmartNotifications = (): Notification[] => {
    const baseNotifications: { [key: string]: Notification[] } = {
      merchant: [
        {
          id: '1',
          type: 'order',
          title: '  ! ',
          message: '  450     -   ',
          timestamp: new Date(Date.now() - 2 * 60 * 1000),
          read: false,
          priority: 'high',
          userType: 'merchant',
          actionButton: {
            label: ' ',
            action: () => console.log('  ')
          }
        },
        {
          id: '2',
          type: 'warning',
          title: '  ',
          message: '3     -   ',
          timestamp: new Date(Date.now() - 15 * 60 * 1000),
          read: false,
          priority: 'medium',
          userType: 'merchant'
        },
        {
          id: '3',
          type: 'success',
          title: '  ! ',
          message: '    25%  ',
          timestamp: new Date(Date.now() - 30 * 60 * 1000),
          read: true,
          priority: 'medium',
          userType: 'merchant'
        },
        {
          id: '4',
          type: 'payment',
          title: '  ',
          message: '   1,200   3 ',
          timestamp: new Date(Date.now() - 45 * 60 * 1000),
          read: false,
          priority: 'medium',
          userType: 'merchant'
        },
        {
          id: '5',
          type: 'info',
          title: '   ',
          message: '    ',
          timestamp: new Date(Date.now() - 2 * 60 * 60 * 1000),
          read: true,
          priority: 'low',
          userType: 'merchant'
        }
      ],
      supplier: [
        {
          id: '6',
          type: 'order',
          title: '    ! ',
          message: ' 500      15,000 ',
          timestamp: new Date(Date.now() - 5 * 60 * 1000),
          read: false,
          priority: 'urgent',
          userType: 'supplier',
          actionButton: {
            label: ' ',
            action: () => console.log('  ')
          }
        },
        {
          id: '7',
          type: 'shipping',
          title: '   ',
          message: '12      -  ',
          timestamp: new Date(Date.now() - 20 * 60 * 1000),
          read: false,
          priority: 'high',
          userType: 'supplier'
        },
        {
          id: '8',
          type: 'warning',
          title: '   ',
          message: '5      ',
          timestamp: new Date(Date.now() - 1 * 60 * 60 * 1000),
          read: false,
          priority: 'high',
          userType: 'supplier'
        },
        {
          id: '9',
          type: 'success',
          title: '   ',
          message: '   8,500   4 ',
          timestamp: new Date(Date.now() - 2 * 60 * 60 * 1000),
          read: true,
          priority: 'medium',
          userType: 'supplier'
        }
      ],
      shipping: [
        {
          id: '10',
          type: 'warning',
          title: '  ! ',
          message: ' VIP    -   100 ',
          timestamp: new Date(Date.now() - 1 * 60 * 1000),
          read: false,
          priority: 'urgent',
          userType: 'shipping',
          actionButton: {
            label: ' ',
            action: () => console.log('   ')
          }
        },
        {
          id: '11',
          type: 'info',
          title: '  ',
          message: '      -  20 ',
          timestamp: new Date(Date.now() - 10 * 60 * 1000),
          read: false,
          priority: 'medium',
          userType: 'shipping'
        },
        {
          id: '12',
          type: 'success',
          title: '   ! ',
          message: '15      -  !',
          timestamp: new Date(Date.now() - 30 * 60 * 1000),
          read: true,
          priority: 'medium',
          userType: 'shipping'
        },
        {
          id: '13',
          type: 'warning',
          title: '  ',
          message: '      -   ',
          timestamp: new Date(Date.now() - 45 * 60 * 1000),
          read: false,
          priority: 'high',
          userType: 'shipping'
        }
      ],
      customer: [
        {
          id: '14',
          type: 'shipping',
          title: '  ! ',
          message: ' #12345   -    ',
          timestamp: new Date(Date.now() - 15 * 60 * 1000),
          read: false,
          priority: 'high',
          userType: 'customer',
          actionButton: {
            label: ' ',
            action: () => console.log('  ')
          }
        },
        {
          id: '15',
          type: 'success',
          title: '  ! ',
          message: ' #12344   -   !',
          timestamp: new Date(Date.now() - 2 * 60 * 60 * 1000),
          read: false,
          priority: 'medium',
          userType: 'customer'
        },
        {
          id: '16',
          type: 'info',
          title: '  ! ',
          message: ' 20%    -  ',
          timestamp: new Date(Date.now() - 4 * 60 * 60 * 1000),
          read: true,
          priority: 'low',
          userType: 'customer'
        }
      ]
    };

    return baseNotifications[userType] || [];
  };

  //  
  useEffect(() => {
    console.debug('[NotificationSystem] mounting, generating initial notifications for', userType);
    const initialNotifications = generateSmartNotifications();
    setNotifications(initialNotifications);
    setUnreadCount(initialNotifications.filter(n => !n.read).length);
  }, [userType]);

  //         
  useEffect(() => {
    const onOpen = () => {
      console.debug('[NotificationSystem] received open-notifications event');
      setIsOpen(true);
    };
    window.addEventListener('open-notifications', onOpen as EventListener);
    return () => window.removeEventListener('open-notifications', onOpen as EventListener);
  }, []);

  //        
  useEffect(() => {
    console.debug('[NotificationSystem] dispatching notifications-updated, unreadCount=', unreadCount);
    const ev = new CustomEvent('notifications-updated', { detail: { unreadCount } });
    window.dispatchEvent(ev);
  }, [unreadCount]);

  //     
  useEffect(() => {
    const interval = setInterval(() => {
      addRandomNotification();
    }, 30000); //  30 

    return () => clearInterval(interval);
  }, [userType]);

  //   
  const addRandomNotification = () => {
    const newNotificationTemplates: { [key: string]: Omit<Notification, 'id' | 'timestamp'>[] } = {
      merchant: [
        {
          type: 'order',
          title: ' ! ',
          message: `  ${Math.floor(Math.random() * 1000) + 100}   `,
          read: false,
          priority: 'medium',
          userType: 'merchant'
        },
        {
          type: 'message',
          title: '   ',
          message: '     ',
          read: false,
          priority: 'medium',
          userType: 'merchant'
        }
      ],
      supplier: [
        {
          type: 'order',
          title: '    ',
          message: ` ${Math.floor(Math.random() * 100) + 20}    `,
          read: false,
          priority: 'medium',
          userType: 'supplier'
        }
      ],
      shipping: [
        {
          type: 'info',
          title: '   ',
          message: `${Math.floor(Math.random() * 5) + 1}    `,
          read: false,
          priority: 'medium',
          userType: 'shipping'
        }
      ],
      customer: [
        {
          type: 'info',
          title: '   ',
          message: '     ',
          read: false,
          priority: 'medium',
          userType: 'customer'
        }
      ]
    };

    const templates = newNotificationTemplates[userType] || [];
    if (templates.length === 0) return;

    const template = templates[Math.floor(Math.random() * templates.length)];
    const newNotification: Notification = {
      ...template,
      id: `new_${Date.now()}`,
      timestamp: new Date()
    };

    setNotifications(prev => [newNotification, ...prev]);
    setUnreadCount(prev => prev + 1);
    
    //   
    playNotificationSound();
  };

  //   
  const playNotificationSound = () => {
    if (audioRef.current) {
      audioRef.current.play().catch(() => {
        //       
      });
    }
  };

  //   
  const markAsRead = (id: string) => {
    setNotifications(prev =>
      prev.map(notification =>
        notification.id === id
          ? { ...notification, read: true }
          : notification
      )
    );
    setUnreadCount(prev => Math.max(0, prev - 1));
  };

  //  
  const deleteNotification = (id: string) => {
    setNotifications(prev => {
      const notificationToDelete = prev.find(n => n.id === id);
      const newNotifications = prev.filter(n => n.id !== id);
      
      if (notificationToDelete && !notificationToDelete.read) {
        setUnreadCount(prevCount => Math.max(0, prevCount - 1));
      }
      
      return newNotifications;
    });
  };

  //     
  const markAllAsRead = () => {
    setNotifications(prev =>
      prev.map(notification => ({ ...notification, read: true }))
    );
    setUnreadCount(0);
  };

  //  
  const filteredNotifications = notifications.filter(notification => {
    switch (filter) {
      case 'unread':
        return !notification.read;
      case 'high':
        return notification.priority === 'high' || notification.priority === 'urgent';
      default:
        return true;
    }
  });

  //    
  const getNotificationIcon = (type: Notification['type']) => {
    const icons = {
      success: CheckCircle,
      warning: AlertTriangle,
      error: AlertTriangle,
      info: Info,
      order: Box,
      payment: DollarSign,
      shipping: Truck,
      message: MessageSquare,
      urgent: AlertTriangle
    };
    const Icon = icons[type] || Info;
    return <Icon size={20} />;
  };

  //   
  const getPriorityColor = (priority: Notification['priority']) => {
    const colors = {
      low: 'border-l-gray-400 bg-gray-50',
      medium: 'border-l-blue-400 bg-blue-50',
      high: 'border-l-orange-400 bg-orange-50',
      urgent: 'border-l-red-400 bg-red-50'
    };
    return colors[priority];
  };

  //    
  const getTypeColor = (type: Notification['type']) => {
    const colors = {
      success: 'text-green-600',
      warning: 'text-orange-600',
      error: 'text-red-600',
      info: 'text-blue-600',
      order: 'text-purple-600',
      payment: 'text-green-600',
      shipping: 'text-blue-600',
      message: 'text-indigo-600',
      urgent: 'text-red-600'
    };
    return colors[type] || 'text-gray-600';
  };

  return (
    <>
      {/*             */}

      {/*   */}
      <AnimatePresence>
        {isOpen && (
          <>
            {/*   */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              onClick={() => setIsOpen(false)}
              className="fixed inset-0 bg-black/20 z-40"
            />
            
            {/*   */}
            <motion.div
              initial={{ opacity: 0, y: -20, scale: 0.95 }}
              animate={{ opacity: 1, y: 0, scale: 1 }}
              exit={{ opacity: 0, y: -20, scale: 0.95 }}
              transition={{ duration: 0.2 }}
              className="fixed top-16 right-4 w-96 bg-white rounded-2xl shadow-2xl border border-gray-200 z-50 max-h-[80vh] flex flex-col"
            >
              {/*   */}
              <div className="flex items-center justify-between p-4 border-b border-gray-100">
                <div className="flex items-center gap-3">
                  <Bell size={20} className="text-gray-700" />
                  <h3 className="font-semibold text-gray-900"></h3>
                  {unreadCount > 0 && (
                    <span className="bg-red-100 text-red-600 text-xs font-medium px-2 py-1 rounded-full">
                      {unreadCount} 
                    </span>
                  )}
                </div>
                <button
                  onClick={() => setIsOpen(false)}
                  className="p-1 hover:bg-gray-100 rounded-lg transition-colors"
                >
                  <X size={18} />
                </button>
              </div>

              {/*  */}
              <div className="p-3 border-b border-gray-100">
                <div className="flex gap-2">
                  {[
                    { key: 'all', label: '', count: notifications.length },
                    { key: 'unread', label: ' ', count: unreadCount },
                    { key: 'high', label: ' ', count: notifications.filter(n => n.priority === 'high' || n.priority === 'urgent').length }
                  ].map((filterOption) => (
                    <button
                      key={filterOption.key}
                      onClick={() => setFilter(filterOption.key as any)}
                      className={`px-3 py-1 rounded-full text-sm transition-colors ${
                        filter === filterOption.key
                          ? 'bg-blue-100 text-blue-700 font-medium'
                          : 'text-gray-600 hover:bg-gray-100'
                      }`}
                    >
                      {filterOption.label} ({filterOption.count})
                    </button>
                  ))}
                </div>
              </div>

              {/*   */}
              <div className="flex-1 overflow-y-auto">
                {filteredNotifications.length === 0 ? (
                  <div className="p-8 text-center text-gray-500">
                    <Bell size={48} className="mx-auto mb-4 text-gray-300" />
                    <p>  </p>
                  </div>
                ) : (
                  <div className="p-2">
                    {filteredNotifications.map((notification) => (
                      <motion.div
                        key={notification.id}
                        initial={{ opacity: 0, x: 20 }}
                        animate={{ opacity: 1, x: 0 }}
                        className={`border-l-4 rounded-lg p-3 mb-2 cursor-pointer transition-all hover:shadow-md ${
                          getPriorityColor(notification.priority)
                        } ${!notification.read ? 'bg-opacity-100' : 'bg-opacity-50'}`}
                        onClick={() => !notification.read && markAsRead(notification.id)}
                      >
                        <div className="flex items-start gap-3">
                          <div className={getTypeColor(notification.type)}>
                            {getNotificationIcon(notification.type)}
                          </div>
                          
                          <div className="flex-1 min-w-0">
                            <div className="flex items-start justify-between gap-2">
                              <h4 className={`font-medium text-sm ${!notification.read ? 'text-gray-900' : 'text-gray-600'}`}>
                                {notification.title}
                              </h4>
                              <div className="flex items-center gap-1">
                                {!notification.read && (
                                  <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                                )}
                                <button
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    deleteNotification(notification.id);
                                  }}
                                  className="p-1 hover:bg-gray-200 rounded opacity-0 group-hover:opacity-100 transition-opacity"
                                >
                                  <X size={12} />
                                </button>
                              </div>
                            </div>
                            
                            <p className="text-xs text-gray-600 mt-1 line-clamp-2">
                              {notification.message}
                            </p>
                            
                            <div className="flex items-center justify-between mt-2">
                              <span className="text-xs text-gray-500">
                                {notification.timestamp.toLocaleTimeString('ar-EG', {
                                  hour: '2-digit',
                                  minute: '2-digit'
                                })}
                              </span>
                              
                              {notification.actionButton && (
                                <button
                                  onClick={(e) => {
                                    e.stopPropagation();
                                    notification.actionButton!.action();
                                  }}
                                  className="text-xs bg-blue-600 text-white px-2 py-1 rounded-full hover:bg-blue-700 transition-colors"
                                >
                                  {notification.actionButton.label}
                                </button>
                              )}
                            </div>
                          </div>
                        </div>
                      </motion.div>
                    ))}
                  </div>
                )}
              </div>

              {/*   */}
              {notifications.length > 0 && (
                <div className="p-3 border-t border-gray-100">
                  <div className="flex justify-between">
                    <button
                      onClick={markAllAsRead}
                      className="text-sm text-blue-600 hover:text-blue-700 font-medium"
                      disabled={unreadCount === 0}
                    >
                          
                    </button>
                    <button
                      className="text-sm text-gray-500 hover:text-gray-700"
                    >
                      
                    </button>
                  </div>
                </div>
              )}
            </motion.div>
          </>
        )}
      </AnimatePresence>

      {/*    */}
      <audio
        ref={audioRef}
        preload="auto"
      >
        {/*      */}
        <source src="data:audio/wav;base64,UklGRnoGAABXQVZFZm10IBAAAAABAAEAQB8AAEAfAAABAAgAZGF0YQoGAACBhYqFbF1fdJivrJBhNjVgodDbq2EcBj+a2/LDciUFLIHO8tiJNwgZaLvt559NEAxQp+PwtmMcBjiR1/LMeSwFJHfH8N2QQAoUXrTp66hVFApGn+DyvmIcBjaP2fPFdiMFKHnN9NSLU..." />
      </audio>
    </>
  );
};

export default NotificationSystem;